



































































































































.filt-containt {
    width: 100%;
    align-items: center;
    border-top: 1px solid var(--weak-border-color);
    display: flex;
    justify-content: space-between;
    .select-box {
        flex: 1;
        .single-box {
            display: flex;
            align-items: center;
            flex: 1;
            .title {
                color: var(--third-text-color);
                font-size: 12px;
            }
            .select-list {
                flex-grow: 1;
                overflow: hidden;
                position: relative;
                list-style: none;
                display: flex;
                .select-item {
                    display: flex;
                    margin: 0 15px;
                    align-items: center;
                    border: 1px solid transparent;
                    border-radius: 2px;
                    max-width: 266px;
                    cursor: pointer;
                    color: var(--secondary-text-color);
                    &.activeted {
                        color: #5e80eb;
                        border: 1px solid #5e80eb;
                        background-color: #fff;
                        border-radius: 2px;
                        max-width: 266px;
                        margin: 8px 12px;
                        padding: 3px 12px;
                    }
                }
            }
        }
        .btn-box {
            width: 100%;
            height: 40px;
            display: flex;
            align-items: center;
            justify-content: center;
            .i-btn {
                &.i-btn-cancel {
                    height: 28px;
                    width: 60px;
                    overflow: hidden;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border: 1px solid #a7b5d3;
                    border-radius: 2px;
                    font-size: 12px;
                    letter-spacing: 0;
                    cursor: pointer;
                    margin-bottom: 11px;
                    margin-right: 12px;
                    &:hover {
                        color: var(--theme-color);
                        border-color: var(--theme-color);
                    }
                }
                &.i-btn-sure {
                    height: 28px;
                    width: 60px;
                    overflow: hidden;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border: 1px solid #a7b5d3;
                    border-radius: 2px;
                    font-size: 12px;
                    letter-spacing: 0;
                    cursor: pointer;
                    margin-bottom: 11px;
                    background: var(--theme-color);
                    color: #fff;
                    &:hover {
                        background: var(--theme-color-level5);
                    }
                }
            }
        }
        &.activeted {
            background: #fff;
        }
    }
    .op-icon {
        display: flex;
        justify-content: center;
        min-width: 60px;
        height: 24px;
        padding: 0 5px;
        background: #fff;
        border: 1px solid #e2e2e2;
        border-radius: 2px;
        font-size: 12px;
        color: #5b6373;
        letter-spacing: 0;
        align-items: center;
        cursor: pointer;
    }
}
