.page-container {
  display: flex;
  color: var(--secondary-text-color);
  font-size: 12px;
}
.page-container .page-total {
  margin-right: 12px;
}
.page-container .page-num {
  margin-right: 15px;
}
.page-container .page-num input {
  box-sizing: border-box;
  width: 32px;
  height: 24px;
  margin: 0 3px;
  padding: 0;
  outline: 0;
  text-align: center;
  background: #fff;
  color: var(--primary-text-color);
  font-size: 12px;
  border-radius: var(--radius-size);
  border: 1px solid var(--strong-border-color);
  transition: border-color 0.3s;
}
.page-container .page-num input:hover {
  border-color: var(--theme-color);
}
.page-container .op-page {
  margin-right: 20px;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: "tnum";
  display: inline-block;
  vertical-align: middle;
}
.page-container .op-page > span {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: "tnum";
  display: inline-block;
}
.page-container .page-range {
  cursor: pointer;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-variant: tabular-nums;
  list-style: none;
  font-feature-settings: "tnum";
  position: relative;
  display: inline-block;
  margin-left: 20px;
  outline: 0;
  height: 24px;
  vertical-align: middle;
}
.page-container .page-range span {
  font-size: 12px;
  color: var(--primary-text-color);
}
.page-container .page-range .select {
  position: relative;
}
.page-container .page-range .select i.kdicon-arrow-down {
  font-size: 16px;
  color: var(--secondary-text-color);
}
.page-container .page-range .select i.kdicon-arrow-down.active {
  transform: rotate(180deg);
}
.page-container .page-range .page-range_list {
  max-width: 320px;
  box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2);
  position: absolute;
  max-height: 336px;
  background: #fff;
  z-index: 1050;
  right: -8px;
  padding: 8px 0;
  overflow-y: auto;
}
.page-container .page-range .page-range_list .page-range_item {
  position: relative;
  padding: 7px 12px;
  color: var(--primary-text-color);
  font-size: 12px;
  line-height: 18px;
  cursor: pointer;
  transition: background-color 0.3s;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
}
.page-container .page-range .page-range_list .page-range_item.act {
  color: var(--theme-color);
}
.page-container .page-range .page-range_list .page-range_item:hover {
  background-color: var(--disabled-contain-bg);
}
.page-container .page-range .page-range_list .page-range_item .item_zh {
  display: inline-block;
  width: 78px;
}
.page-container .page-range .page-range_list .page-range_item .item_us {
  display: inline-block;
  width: 99px;
}
.page-container .kdicon {
  font-size: 16px;
  cursor: pointer;
}
.page-container .kdicon.dis {
  color: var(--disabled-border-color);
}
.page-container .kdicon:hover {
  color: var(--theme-color);
}
