.el-table th.el-table__cell > .cell {
  color: #333;
  font-size: 12px;
  font-weight: normal;
}
.i-btn {
  width: 60px;
  box-sizing: border-box;
  display: inline-block;
  position: relative;
  height: 28px;
  line-height: 24px;
  border-radius: 2px;
  font-size: 12px;
  cursor: pointer;
  outline: 0;
  white-space: nowrap;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}
.i-btn.sure {
  border: 1px solid var(--theme-color);
  color: #fff;
  background-color: var(--theme-color);
}
.i-btn.sure:hover {
  background-color: var(--theme-color-level5);
  border-color: var(--theme-color-level5);
}
.i-btn.view-log {
  min-width: 88px;
  max-width: 240px;
}
.el-table__fixed,
.el-table__fixed-right {
  height: 100%;
  box-shadow: none;
}
