































































































































.page-container {
  display: flex;
  color: var(--secondary-text-color);
  font-size: 12px;
  .page-total {
    margin-right: 12px;
  }
  .page-num {
    margin-right: 15px;
    input {
      box-sizing: border-box;
      width: 32px;
      height: 24px;
      margin: 0 3px;
      padding: 0;
      outline: 0;
      text-align: center;
      background: #fff;
      color: var(--primary-text-color);
      font-size: 12px;
      border-radius: var(--radius-size);
      border: 1px solid var(--strong-border-color);
      transition: border-color 0.3s;
      &:hover {
        border-color: var(--theme-color);
      }
    }
  }
  .op-page {
    margin-right: 20px;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-variant: tabular-nums;
    list-style: none;
    font-feature-settings: "tnum";
    display: inline-block;
    vertical-align: middle;
    > span {
      box-sizing: border-box;
      margin: 0;
      padding: 0;
      font-variant: tabular-nums;
      list-style: none;
      font-feature-settings: "tnum";
      display: inline-block;
    }
    .first-page {
    }
    .next-page {
    }
    .pre-page {
    }
    .last-page {
    }
  }
  .page-range {
    cursor: pointer;
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-variant: tabular-nums;
    list-style: none;
    font-feature-settings: "tnum";
    position: relative;
    display: inline-block;
    margin-left: 20px;
    outline: 0;
    height: 24px;
    vertical-align: middle;
    span {
      font-size: 12px;
      color: var(--primary-text-color);
    }
    .select {
      position: relative;
      i.kdicon-arrow-down {
        font-size: 16px;
        color: var(--secondary-text-color);
        &.active {
          transform: rotate(180deg);
        }
      }
    }

    .page-range_list {
      max-width: 320px;
      box-shadow: 0 4px 10px 0 rgba(0, 0, 0, 0.2);
      position: absolute;
      max-height: 336px;
      background: #fff;
      z-index: 1050;
      right: -8px;
      padding: 8px 0;
      overflow-y: auto;
      .page-range_item {
        // min-width: 78px;
        position: relative;
        padding: 7px 12px;
        color: var(--primary-text-color);
        font-size: 12px;
        line-height: 18px;
        overflow: hidden;
        cursor: pointer;
        transition: background-color 0.3s;
        overflow: hidden;
        text-overflow: ellipsis;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        &.act {
          color: var(--theme-color);
        }
        &:hover {
          background-color: var(--disabled-contain-bg);
        }
        .item_zh {
          display: inline-block;
          width: 78px;
        }
        .item_us {
          display: inline-block;
          width: 99px;
        }
      }
    }
  }
  .kdicon {
    font-size: 16px;
    cursor: pointer;
  }

  .kdicon.dis {
    color: var(--disabled-border-color);
  }
  .kdicon:hover {
    color: var(--theme-color);
  }
}
